<script>
	import { _, locale } from '../services/i18n';
    import {
      Collapse,
      Navbar,
      NavbarToggler,
      NavbarBrand,
      Nav,
      NavItem,
      NavLink,
      UncontrolledDropdown,
      DropdownToggle,
      DropdownMenu,
      DropdownItem
    } from 'sveltestrap';
  
    let isOpen = false;
  
    function handleUpdate(event) {
      isOpen = event.detail.isOpen;
    }

</script>
  
<Navbar color="primary" dark expand="md">
    <NavbarBrand href="/"><strong>JH</strong> <small class="md">{$_('home.subtopic', { default: "~" })}</small></NavbarBrand>
    <NavbarToggler on:click={() => (isOpen = !isOpen)} />
    <Collapse {isOpen} navbar expand="md" on:update={handleUpdate}>
        <Nav class="ml-auto" navbar>
        <NavItem>
            <NavLink href="{$_('contact.linkedin')}" target="_blank">Linkedin</NavLink>
        </NavItem>
        <NavItem>
            <NavLink href="{$_('contact.github')}" target="_blank">GitHub</NavLink>
        </NavItem>
        <UncontrolledDropdown nav inNavbar>
            <DropdownToggle nav caret id="lang-dropdown" title="Language">💬 </DropdownToggle>
            <DropdownMenu right>
                <DropdownItem on:click={() => locale.set("en")}>English</DropdownItem>
                <DropdownItem on:click={() => locale.set("es")}>Español</DropdownItem>
                <DropdownItem on:click={() => locale.set("jp")}>日本語</DropdownItem>
            </DropdownMenu>
        </UncontrolledDropdown>
    </Nav>
</Collapse>
</Navbar>

