import {
    init,
    addMessages,
    getLocaleFromNavigator,
    locale,
    _
} from 'svelte-i18n';

import en from '../i18n/en.json';
import es from '../i18n/es.json';
import jp from '../i18n/jp.json';

addMessages('en', en);
addMessages('es', es);
addMessages('jp', jp);

init({
    fallbackLocale: 'en',
    initialLocale: getLocaleFromNavigator(),
});

export {
    _,
    locale
};